.home-bg{background:url(/assets/img/open_legend_saryth_navigator_night_in_forest.jpg)left top no-repeat fixed #424242;background-size:cover}.home-bg .banner-logo{width:70%}.home-bg .dark-bg{background:rgba(66,66,66,.7);color:#fff}.home-bg .tri-grid [layout-padding]>div{background:#424242;padding:0;width:100%}.home-bg .tri-grid a{transition:all .2s ease-in-out;opacity:.7}.home-bg .tri-grid a:hover{opacity:1}@media (max-width:1024px){.tri-grid [layout-padding]>div{width:33%}.home-bg .banner-logo{width:100%}}